import './App.css';
import React from 'react';

export default class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      method: "date",
      data: []
    }

    this.sortBy = this.sortBy.bind(this);
  }

  componentDidMount() {
    const self = this;
    try {
      window.chrome.storage.local.get(['data'], function (result) {
        var data = result["data"];
        if (!data) {
          console.log("no data found!");
          return;
        }
        self.setState({
          data: data
        })
      })
    } catch (e) {
      console.log(e);
    }
  }

  sortBy(method) {
    if (method === "amount") {
      var sorted = this.state.data.sort(function (a, b) {
        return b["amount"] - a["amount"];
      })
      this.setState({
        data: sorted,
        method: method
      })
    } else if (method === "date") {
      var sorted = this.state.data.sort(function (a, b) {
        return Date.parse(b["date"]) - Date.parse(a["date"]) || b["amount"] - a["amount"];
      })
      this.setState({
        data: sorted,
        method: method
      })
    } else if (method === "account") {
      var sorted = this.state.data.sort(function (a, b) {
        return a["account"].localeCompare(b["account"]);
      })
      this.setState({
        data: sorted,
        method: method
      })
    } else if (method === "location") {
      var sorted = this.state.data.sort(function (a, b) {
        return a["location"].localeCompare(b["location"]) || b["amount"] - a["amount"];
      })
      this.setState({
        data: sorted,
        method: method
      })
    }
  }

  render() {
    return (
      <div id="main">
        <h1>Budget Buddy Analyzer</h1>
        <div className="table_container">
          <table>
            <tr className="table_header">
              <th className={this.state.method === "location" ? 'selected' : null} onClick={() => this.sortBy("location")}>Location</th>
              <th className={this.state.method === "amount" ? 'selected' : null} onClick={() => this.sortBy("amount")}>Amount</th>
              <th className={this.state.method === "account" ? 'selected' : null} onClick={() => this.sortBy("account")}>Account</th>
              <th className={this.state.method === "date" ? 'selected' : null} onClick={() => this.sortBy("date")}>Date</th>
              <th className={this.state.method === "date" ? 'selected' : null} onClick={() => this.sortBy("date")}>Time</th>
            </tr>
            {this.state.data.map(e =>
              <tr key={e.id}>
                <td>{e.location}</td>
                <td>${e.amount}</td>
                <td>{e.account}</td>
                <td>{new Date(Date.parse(e.date)).toLocaleDateString()}</td>
                <td>{new Date(Date.parse(e.date)).toLocaleTimeString()}</td>
              </tr>)}
          </table>
        </div>
      </div>
    )
  }
}
